body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.disabled {
  opacity: 0.6;
  cursor: not-allowed; }

.genericButton {
  background-color: #d9d9d9;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 7px;
  padding-bottom: 7px;
  border-radius: 35px;
  font-size: 100%;
  border-width: 0px;
  outline: 0px; }

.genericButton:focus {
  outline: 0px; }

@media screen and (max-width: 576px) {
  .colorPickerButtonContainer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0px;
    margin-top: 5px;
    margin-bottom: 7px; } }

@media screen and (min-width: 577px) {
  .colorPickerButtonContainer {
    display: flex;
    align-items: center;
    justify-content: flex-end; } }

.container-fluid.header {
  margin-top: 30px; }
  .container-fluid.header .row {
    padding: 0px;
    margin: 30px 0px; }
    .container-fluid.header .row .monthName {
      color: gray; }

.container-fluid {
  padding: 0px;
  margin: 0px; }

.date {
  flex-grow: 1;
  /* flex-basis: 14.20%; */
  min-width: 14.20%;
  max-width: 14.20%;
  margin: 0px;
  border: 1px solid #e6e6e6;
  padding: 0px; }

.allDates {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 10px;
  background-color: white; }

.calenderContainer {
  background-color: #e5d1d2;
  padding: 0px;
  display: flex;
  align-items: center;
  display: flex;
  margin-bottom: 10px; }

.dateRow {
  margin: 10px;
  width: 100vw;
  margin: 10px; }

@media only screen and (max-width: 760px) {
  .taskInCalender {
    padding: 2px;
    border-radius: 5px;
    margin-bottom: 5px;
    font-size: 90%;
    word-break: normal;
    word-wrap: break-word; } }

.eventInCalender {
  width: 100%;
  padding: 3%;
  border-radius: 5px;
  margin-bottom: 5px;
  word-break: normal;
  word-wrap: break-word; }

.individualDate {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 3%;
  width: 100%;
  height: 100%; }

.dateIndex {
  width: 100%;
  padding-bottom: 4%;
  color: #737373;
  font-weight: normal; }

.MuiDialog-paperWidthSm-8 {
  max-width: 55vw !important;
  min-width: 35vw !important;
  min-height: 30vh !important;
  padding: 1% !important; }

.modalTitle {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

#responsive-dialog-title {
  padding-right: 0px;
  padding-top: 0px;
  padding-bottom: 0px; }

.modalContentHeader {
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 0px;
  display: flex;
  justify-content: space-between; }

.modalCloseButton {
  padding-top: 0px;
  padding-bottom: 5px !important;
  padding-left: 10px !important;
  padding-right: 10px !important;
  background-color: transparent;
  border-width: 0px;
  border-color: transparent; }

.modalCloseButton:hover {
  padding-top: 0px;
  padding-bottom: 5px !important;
  padding-left: 10px !important;
  padding-right: 10px !important;
  outline: 0px;
  background-color: #e0e0d1; }

.editForm {
  width: 60%;
  padding: 0px; }

.addTaskButton {
  background-color: #d9d9d9;
  padding-left: 2vw;
  padding-right: 2vw;
  padding-top: 8px;
  padding-bottom: 8px;
  border-radius: 35px;
  font-size: 100%;
  min-width: 110px;
  min-height: 30px;
  border-width: 0px; }

.addTaskButton:active {
  outline: 0px; }

.individualEvent {
  display: flex;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content; }

@media only screen and (min-width: 761px) and (max-width: 1000px) {
  .MuiDialog-paperWidthSm-8 {
    max-width: 70vw !important;
    min-width: 50vw !important;
    min-height: 30vh !important;
    padding: 1% !important; } }

@media only screen and (max-width: 760px) {
  .MuiDialog-paperWidthSm-8 {
    max-width: 80vw !important;
    min-width: 60vw !important;
    min-height: 30vh !important;
    padding: 1% !important; }
  .addTaskButton {
    background-color: #d9d9d9;
    padding-left: 3vw;
    padding-right: 3vw;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 35px;
    font-size: 90%;
    min-width: 110px;
    min-height: 30px;
    border-width: 0px;
    margin-bottom: 10px; } }

.icons {
  width: 32px;
  height: auto;
  padding: 0px;
  margin-bottom: 7px; }

.modalContent {
  padding: 10px;
  padding-bottom: 25px; }

.cancelIcon {
  width: 30px;
  height: auto;
  padding: 0px;
  margin-bottom: 7px;
  margin-right: 10px;
  margin-left: 15px; }

.okayIcon {
  width: 35px;
  height: auto;
  padding: 0px;
  margin-bottom: 7px;
  margin-left: 25px; }

.closeButton {
  display: flex;
  justify-content: flex-end;
  padding: 0px !important; }

.closeButtonRow {
  padding-right: 0px !important;
  margin-right: 0px; }

.container-fluid {
  padding: 0px;
  margin: 0px; }

.singleday {
  flex-grow: 1;
  min-width: 14.20%;
  max-width: 14.20%;
  margin: 0px; }

.dayNameContainer {
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  padding: 0px;
  display: flex;
  align-items: center;
